<template>
  <div>
    <audio id="audio"></audio>
    <div class="iconfont icon-sound"></div>
    <el-progress :show-text="false" :stroke-width="5" :percentage="Recordingtage" color="#2E70ED"></el-progress>
    <div class="time-text">{{Recordingtime}}</div>
    <el-button class="record-btn" @click="startRecording" v-if="!bengin">开始录音</el-button>
    <el-button class="record-btn" @click="endRecording" v-if="bengin">结束录音</el-button>
  </div>
</template>

<script>
// import '@/assets/js/recorder.mp3.min';
// import '../js/luyin'

// import Recorder from "recorder-js";    // 引入录音插件
// const audioContext = new (window.AudioContext || window.webkitAudioContext)();
// const recorder = new Recorder(audioContext, {
//   // An array of 255 Numbers
//   // You can use this to visualize the audio stream
//   // If you use react, check out react-wave-stream
//   onAnalysed: data => {
//     console.log("recorder data",data, 321)
//   }
// });

// console.log(recorder);
import "@/assets/recordermaster/src/recorder-core.js";
import "@/assets/recordermaster/src/extensions/waveview.js";
import "@/assets/recordermaster/src/extensions/jquery.js";

export default {
  name: "recorderDemo",
  data() {
    return {
      bengin: false,
      Recordingtime: "00",
      Recordingtage: "30",
      Recordingnum: ""
    };
  },
  created() {},
  methods: {
    startRecording() {
      this.bengin = true;
      recopen();
    },
    endRecording() {
      this.bengin = false;
      recstop();
    }
  }
};

var rec;
// var r = null;
var time = 0;
// var timeInterval = null;
console.log(time);

function recopen(){  //打开录音
	// var type=$("[name=type]:checked").val();
	// var bit=+$(".bit").val();
  // var sample=+$(".sample").val();
  var type = "mp3"; // 类型
  var bit = 16; // kbps mp3、wav取值8位16位
  var sample = 16000; // mp3标准值，wav任意

	// var wave,waveSet=$(".recwaveSet")[0].checked;
	rec=window.Recorder({
    type:type,
    bitRate:bit,
    sampleRate:sample,
    onProcess:function(buffers,level,time,sampleRate){
      console.log('成功参数1：', buffers);
      console.log('成功参数1：', level);
      console.log('成功参数1：', time);
      console.log('成功参数1：', sampleRate);
      
			// $(".recpowerx").css("width",level+"%");
			// $(".recpowert").html(time+"/"+level);
			
			// waveSet && wave.input(buffers[buffers.length-1],level,sampleRate);
		}
	});
	rec.open(
		function(){
			console.log("已打开:"+type+" "+bit+"kbps");
			
			// wave=Recorder.WaveView({elem:".recwave"});
			
			setTimeout(function(){  //正常打开录音后开始录音
				recstart();
			},200)
		},
		function(e,isUserNotAllow){
			console.log((isUserNotAllow?"UserNotAllow，":"")+"打开失败："+e);
		}
	);
}

function recstop(batCall){
	console.log('batCall:', batCall);
	if(rec){
		if(!batCall){
			console.log("正在编码"+rec.set.type+"...");
		}
		rec.stop(
		function(blob,time){
      console.log('blob:=======', blob);
			console.log('time:=======', time);
			console.log('l录制完成')
		},function(s){
			console.log("失败："+s);
			batCall&&batCall();
		});
	}
	setTimeout(function(){
		recclose()
	},200)
}


function recstart() {
  if (rec) {
    rec.start();
    console.log("录制中...");
  }
}
function recclose() { // 关闭网页录音功能
	if (rec) {
		rec.close(function() {
			console.log("已关闭");// 控制台提示已关闭
		});
	}
}
</script>


